<template>
  <div class="list_container">
    <!--  -->
    <div class="search_container">
      <div class="search_item">
        <div class="search_lebel"></div>
        <div class="search_content">
          <el-cascader
            clearable
            filterable
            v-model="place"
            :options="cityData"></el-cascader>
        </div>
      </div>
      <div class="search_item">
        <div class="search_lebel"></div>
        <div class="search_content">
          <el-input v-model="pageQuery.visitName" placeholder=""></el-input>
        </div>
      </div>
      <div class="search_item">
        <div class="search_lebel"></div>
        <div class="search_content">
          <el-input v-model="pageQuery.visitPhone" placeholder=""></el-input>
        </div>
      </div>
      <div class="search_item">
        <el-button @click="resetSearchForm" :disabled="pageLoading"></el-button>
      </div>
      <div class="search_item">
        <el-button type="primary" @click="submitSearchForm" :disabled="pageLoading"></el-button>
      </div>
    </div>
    <div class="search_container">
      <div class="search_item">
        <!-- excel -->
        <span class="upload_excel_btn">
          <el-button type="primary"
            :loading="excelLoading"
            @click="importClick">excel</el-button>
          <input type="file" ref="upload-excel" style="display: none" accept=".xlsx,.xls" @change="fileChange">
        </span>
      </div>
      <div class="search_item">
        <el-button type="primary" @click="downlaodTemplate" :disabled="pageLoading">excel</el-button>
      </div>
      <div class="search_item">
        <el-button type="primary" @click="add"></el-button>
      </div>
    </div>
    <!--  -->
    <el-table :data="pageData"
      v-if="showMain"
      border>
      <el-table-column
        align="center"
        label=""
        prop="id">
      </el-table-column>
      <el-table-column
        align="center"
        label=""
        prop="visitName">
      </el-table-column>
      <el-table-column
        align="center"
        label=""
        prop="visitDate">
        <template slot-scope="scope">
          {{scope.row.visitDate && parseTime(scope.row.visitDate, '')}}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        label=""
        prop="visitSource">
      </el-table-column>
      <el-table-column
        align="center"
        label="">
        <template slot-scope="scope">
          {{scope.row.city}}{{scope.row.area}}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        label=""
        prop="isPoor">
      </el-table-column>
      <el-table-column
        align="center"
        label=""
        prop="visitProblemType">
      </el-table-column>
      <el-table-column
        align="center"
        label=""
        prop="effectiveType">
      </el-table-column>
      <el-table-column
        align="center"
        label=""
        prop="invalidType">
      </el-table-column>
      <el-table-column
        align="center"
        label=""
        prop="visitContent">
      </el-table-column>
      <el-table-column
        align="center"
        label="">
        <template slot-scope="scope">
          <el-button type="text" @click="edit(scope.row)"></el-button>
          <el-button type="text" class="del_btn" @click="del(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination_container"
      v-if="showMain">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageQuery.page"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="pageQuery.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog
      width="600px"
      :close-on-click-modal="false"
      :visible.sync="dialogShow"
      :title="formStatus === 'add' ? '' : ''">
      <el-form :model="dialogForm" ref="dialogForm" label-width="100px" class="dialog_form" :rules="rules">
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="visitName">
              <el-input v-model="dialogForm.visitName" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="phone">
              <el-input v-model="dialogForm.phone" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="visitDate">
              <el-date-picker
                class="width100"
                v-model="dialogForm.visitDate"
                type="date"
                placeholder="">
              </el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="visitSource">
            <el-input v-model="dialogForm.visitSource" placeholder=""></el-input>
          </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="">
            <el-cascader
              class="width100"
              clearable
              filterable
              v-model="dialogPlace"
              :options="cityData"></el-cascader>
          </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="isPoor">
              <el-select v-model="dialogForm.isPoor" placeholder="">
                <el-option
                  v-for="item in poorType"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="visitProblemType">
            <el-select v-model="dialogForm.visitProblemType" placeholder="">
              <el-option
                v-for="item in problemType"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="effectiveType">
            <el-select v-model="dialogForm.effectiveType" placeholder="">
              <el-option
                v-for="item in effectiveType"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="invalidType">
            <el-select v-model="dialogForm.invalidType" placeholder="">
              <el-option
                v-for="item in invalidType"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="" prop="visitContent">
          <el-input v-model="dialogForm.visitContent" type="textarea"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import * as ChinaData from 'element-china-area-data'
import { mapActions } from 'vuex'
import DataCofig from '@/config/data'
import Utils from '@/utils'
export default {
  data () {
    return {
      showMain: true,
      dialogShow: false,
      formStatus: 'add',
      CodeToText: {},
      TextToCode: {},
      // 
      cityData: [],
      // 
      excelLoading: false,
      pageLoading: false,
      poorType: [],
      problemType: [],
      effectiveType: [],
      invalidType: [],
      // 
      place: [],
      // 
      dialogPlace: [],
      // 
      dialogForm: {
        id: '',
        visitName: '',
        phone: '',
        visitDate: '',
        visitSource: '',
        city: '',
        area: '',
        isPoor: '',
        visitProblemType: '',
        effectiveType: '',
        invalidType: '',
        visitContent: ''
      },
      pageQuery: {
        city: '',
        area: '',
        visitPlace: '',
        visitName: '',
        visitPhone: '',
        page: 1,
        size: 10
      },
      total: 0,
      pageData: [],
      rules: {
        visitName: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  watch: {
    place (val) {
      // this.pageQuery.visitPlace = val.join(',')
      const city = this.CodeToText[val[0]]
      const area = this.CodeToText[val[1]]
      this.pageQuery.city = city
      this.pageQuery.area = area
    },
    dialogPlace (val) {
      const city = this.CodeToText[val[0]]
      const area = this.CodeToText[val[1]]
      this.dialogForm.city = city
      this.dialogForm.area = area
    }
  },
  mounted () {
    this.initSettingData()
    this.initCityData()
    this.getPageData()
  },
  methods: {
    parseTime: Utils.parseTime,
    ...mapActions([
      'GET_VISIT_LIST',
      'UPLOAD_EXCEL',
      'DOWNLOAD_FILE',
      'POST_VISIT',
      'PUT_VISIT',
      'DELETE_VISIT'
    ]),
    // 
    add () {
      this.formStatus = 'add'
      this.dialogShow = true
    },
    // 
    edit (row) {
      console.log('row:', row)
      for (let key in this.dialogForm) {
        this.dialogForm[key] = row[key]
      }
      const city = this.TextToCode[this.dialogForm.city] || {}
      const area = city[this.dialogForm.area] || {}
      this.dialogPlace = [city.code, area.code]
      this.formStatus = 'edit'
      this.dialogShow = true
    },
    // 
    del (row) {
      this.$confirm('?', '').then(() => {
        this.del2(row)
      })
    },
    async del2 (row) {
      try {
        const res = await this.DELETE_VISIT({ id: row.id })
        console.log('', res)
        this.getPageData()
      } catch (e) {
        console.log(':', e)
      }
    },
    // 
    closeDialog () {
      this.dialogPlace = []
      this.$refs.dialogForm.resetFields()
      this.formStatus = 'add'
      this.dialogShow = false
    },
    // 
    async submit () {
      this.$refs.dialogForm.validate(valid => {
        if (valid) {
          this.submitDlaiogForm()
        }
      })
    },
    async submitDlaiogForm () {
      console.log('dialogForm:', this.dialogForm)
      try {
        const res = this.formStatus === 'add' ? await this.POST_VISIT(this.dialogForm) : await this.PUT_VISIT(this.dialogForm)
        if (res.code === 200) {
          this.closeDialog()
          this.getPageData()
        }
        console.log(':', res)
      } catch (e) {
        console.log(':', e)
      }
    },
    importClick () {
      const dom = this.$refs['upload-excel']
      dom && dom.click()
    },
    async fileChange (e) {
      const file = e.target.files[0]
      const dom = this.$refs['upload-excel']
      dom && (dom.value = '')
      const form = new FormData()
      form.append('file', file)
      try {
        const res = await this.UPLOAD_EXCEL(form)
        console.log(':', res)
        if (res.code === 200) {
          this.pageQuery.page = 1
          this.getPageData()
          this.$message.success(res.message)
        }
      } catch (e) {
        console.log('excel:', e)
      }
    },
    // 
    resetSearchForm () {
      this.place = []
      this.pageQuery.visitName = ''
      this.pageQuery.visitPhone = ''
    },
    // 
    submitSearchForm () {
      this.getPageData()
    },
    // 
    handleCurrentChange (val) {
      console.log(`${val}`)
      this.getPageData()
    },
    // 
    handleSizeChange (val) {
      console.log(`${val}`, this.pageQuery.page)
      this.getPageData()
    },
    // excel
    downlaodTemplate () {
      this.DOWNLOAD_FILE('public/template_excel.xlsx')
    },
    // 
    async getPageData () {
      try {
        const res = await this.GET_VISIT_LIST(this.pageQuery)
        if (res.code === 200 && res.data) {
          console.log('', res.data)
          const resData = res.data
          this.total = resData.total
          this.pageData = resData.data
        }
      } catch (e) {
        console.log(':', e)
      }
    },
    // 
    initCityData () {
      // Object.keys(ChinaData)
      // CodeToText code
      // TextToCode code
      // provinceAndCityData 
      // provinceAndCityDataPlus 
      // regionData 
      // regionDataPlus 
      const henanCode = ChinaData.TextToCode[''].code
      let cityData = []
      for (let i = 0; i < ChinaData.regionData.length; i++) {
        const province = ChinaData.regionData[i]
        if (province.value === henanCode) {
          cityData = province.children
          break
        }
      }
      // 
      this.cityData = cityData
      this.CodeToText = ChinaData.CodeToText
      this.TextToCode = ChinaData.TextToCode['']
      // 
      // this.cityData = ChinaData.regionData
      // 
      // this.cityData = ChinaData.regionData
    },
    // 
    initSettingData () {
      this.poorType = DataCofig.poorType
      this.problemType = DataCofig.problemType
      this.effectiveType = DataCofig.effectiveType
      this.invalidType = DataCofig.invalidType
    }
  }
}
</script>

